<template>
  <div class="adv">
    <!-- 不显示底部导航buttonTabbar -->
    <!-- <img src="../../assets/imgs/gg.jpg" alt /> -->
    <!-- 倒计时用定时器解决   点击跳过广告 就跳转到首页-->
    <button @click="goHome">
      跳过: <strong>{{num}}s</strong> 
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 5,
      //2定时器
      countDown: null
    };
  },
  methods: {
    goHome() {
      this.$router.push("/main/menu");
    }
  },
  //生命周期函数：1.定时器需要放在created()中。因为一进入页面就 开始 倒计时   2.定时器 属于window全局，当路由器切换的时候 必须关闭定时器
  created() {
    // this.$store.state.bottomTabbar = false;
    //倒计时.定时器 需要箭头函数。便于不冲突this
    this.countDown = setInterval(() => {
      //判断if当num==0时跳转到首页
      //this.num--;
      if (this.num == 0) {
        this.$router.push("/main/home");
      } else {
        this.num--;
      }
    }, 1000);
  },
  beforeDestroy() {
    // this.$store.state.bottomTabbar = true;
    
    clearInterval(this.countDown);
  }
};
</script>

<style lang="less" scoped>
div {
  width: 100%;
  height: 667px;
  position: relative;
  background-image: url('../../assets/images/gg.png');
  background-repeat: no-repeat;
  background-position: center center;
  overflow: hidden;
  button {
    // width: 100px;
    position: absolute;
    top: 10px;
    right: 10px;
    height: 30px;
    font-size: 20px;
    font-weight: 80px;
    border-radius: 10px;
    color: #ccc;
    // padding: 10px;
    border: none;
    background-color: rgba(255, 255, 255, 0.486);
    outline: none;
    h3 {
      display: inline-block;
    }
  }
}
</style>